import axios from 'axios'
import { getUTCTimestamp } from './time'
import { storage } from './../storage/storage'
import { config } from './../config/config'

const REST = config.restURL


export const ajax = (path, method = 'GET', body, options = { updateLastActive: true }) => {

  const { updateLastActive } = options

  const lastActive = storage.lastActive || 0

  // if (updateLastActive) {
  //   storage.lastActive = getUTCTimestamp()
  // }


  return axios({
    url: `${REST}${path}`,
    method,
    data: body,
    headers: {
      'last-active': lastActive
    },
  }).then(json => json.data).then(json => {
    storage.lastActive = json.lastActive
    return json.data
  })

}
