import { ajax } from './client'
import { storage } from './../storage/storage'
import { isEmptyObj } from './utils'
import { config } from './../config/config'
import { BankTypes } from './constants'

const MSG_PULLING_INTERVAL = config.pullingInterval

export const Messaging = {

  start() {
    setInterval(async () => {
      const msg = await ajax(`/user/message/${storage.userId}`)
      if (!isEmptyObj(msg)) {
        console.log('msg', msg)
        this.onmessage(msg)
      }
    }, MSG_PULLING_INTERVAL);
  },

  async send(action) {
    ajax('/message', 'PUT', { userId: storage.userId, action })
    console.log('action', action)
  },

  async init(onmessage) {
    this.onmessage = onmessage
    storage.bank = BankTypes.ALFA
    let userId = storage.userId

    if (!userId) {
      userId = await ajax('/user/id')
      storage.userId = userId
      // await ajax('/message', 'PUT', { userId, action: { type: UserActions.NEW_USER } })
    }

    if (storage.touched) {
      this.start()
    }

  }

}


