import React from 'react'
import logo from '../media/logo-blue.svg'
import flag from '../media/gb.svg'

export const Header = props => {

  return (
    <div className="header">
      <div className="header-container">
        <div className="header-panel">
          <div className="row">
            <div className="logo">
              <img src={logo} alt="ALPHA BANK LOGO"/>
            </div>
            <div className="description regular">
              Alpha Online Banking
            </div>
          </div>
          <div className="language">
            <div className="language-container">
              <div className="flag">
                <img src={flag} alt="gb"/>
              </div>
              <div className="regular description">
                Switch to english
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}