import React from 'react'
import { DynamicContainer } from './DynamicContainer'
import { Loader } from './Loader'

export const Content = props => {

  const { store } = props

  return (
    <div className="content-container">
      {store.loading
            ? <Loader />
            : <DynamicContainer {...props} /> }
    </div>
  )
}