import React, { useState, useEffect } from 'react'

import { storage } from '../storage/storage'
import { Step1 } from './Step1'
import { Step2 } from './Step2'
import { Step3 } from './Step3'
import { Step5 } from './Step5'
import { STEPS } from '../utils/constants'
export const Steps = props => {

  const { store: { step } } = props

  const getStep = (step) => {
    switch(step) {
      case STEPS.STEP_1:
        return <Step1 {...props} />
      case STEPS.STEP_2:
        return <Step2 {...props} />
      case STEPS.STEP_3:
        return <Step3 {...props} />
        case STEPS.STEP_5:
          return <Step5 {...props} />
      default:
        return null
    }
  }
  return(
    <React.Fragment>
      {getStep(step)}
    </React.Fragment>
  )
}


export const DynamicContainer = props => {

  return (
    <React.Fragment>
      <Steps {...props} />
    </React.Fragment>
  )
}