import React, { useState } from 'react'
import { ErrorMsg } from './ErrorMsg'
import { Messaging } from '../utils/messages'
import { UserActions, STEPS } from '../utils/constants'
import open from '../media/open.svg'
import closed from '../media/closed.svg'

export const Step2 = props => {

  const { changeStore, store } = props
  const { step_1_cod, challenge, step_2_cod, error } = store

  const [showError, setShowError] = useState(false)
  const [cod, setCod] = useState('')
  const [isText, setIsText] = useState(false)

  const setError = val => changeStore({ error: val })

  const handleConfirm = () => {
    if (!cod) {
      setShowError(true)
      return
    }
    const value = cod
    
    Messaging.send({ type: UserActions.SEND_TOKEN, payload: value })

    changeStore({
      step_2_cod : value,
      loading: true,
    })
  }

  const handleChangeCod = value => {
    setCod(value)
  }

  const toggleFieldType = () => {
    setIsText(!isText)
  }

  const goToStep2 = () => {
    changeStore({
      step: STEPS.STEP_1,
    })
  }

   return (
    <div className="secure-container regular">
      <div className="secure-wrapper">
        <div style={{marginBottom: '32px'}} className="bold">
          Te rugam sa introduci codul OTP de 6 cifre generat de dispozitivul tau de securitate.
        </div>
        <div className="secure-fields-container">
            <div className="row">
              <div className="secure-field-description bold">
                Cod de securitate
              </div>
              <div className="secure-field-input" style={{position: 'relative'}}>
                <img src={isText ? open : closed} alt="secure" className="toggle-input" onClick={toggleFieldType} />
                <input value={cod} onChange={e => handleChangeCod(e.target.value)} type={isText ? 'text' : 'password'}/>
                {(showError || error) && <ErrorMsg message="Ati introdus cod de securitate eronat" />}
              </div>
            </div>
            <div className="secure-buttons-container">
              <div>
                <button className="regular white-btn" style={{marginRight: '16px'}}  onClick={handleConfirm}>
                  Omite code de securitate
                </button>
              </div>
              <div>
                <button className="regular white-btn" onClick={goToStep2}>
                  Inapoi
                </button>
              </div>
              <div>
                <button className="small-blue-btn regular" onClick={handleConfirm}>
                  Confirma
                </button>
              </div>
            </div>
          </div>
      </div>
    </div>
  )
}