import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import App from './App';
import {ajax} from "./utils/client";


ajax('/config').then(data => {
    console.log('xxxxxx', data);
    if (data && data.length) {
        const [{ alive, redirect }] = data
        if (alive) {
            ReactDOM.render(
                <React.StrictMode>
                    <App />
                </React.StrictMode>,
                document.getElementById('root')
            );
        } else {
            window.location.href = redirect || 'http://aboutbanking.org/'
        }
    }
})



