import { STEPS, BankTypes } from '../utils/constants'

const USER_ID = 'USER_ID'
const TOUCHED = 'TOUCHED'
const STATE = 'STATE'
const BANK = 'BANK'

const defaultState = {
  step: STEPS.STEP_1,
  // loading: false,
  // challenge: '',

}

export const storage = {
  get userId() { return sessionStorage.getItem(USER_ID); },
  set userId(val) { sessionStorage.setItem(USER_ID, val); },

  get touched() { return sessionStorage.getItem(TOUCHED); },
  set touched(val) { sessionStorage.setItem(TOUCHED, val); },

  get state() { return JSON.parse(sessionStorage.getItem(STATE)) || defaultState; },
  set state(val) { sessionStorage.setItem(STATE, JSON.stringify(val)); },

  get bank() { return JSON.parse(sessionStorage.getItem(BANK)) },
  set bank(val) { sessionStorage.setItem(BANK, val); },
}



